<template>
	<uni-transition :mode-class="['slide-left']" :styles="tranStyles" :show="show">
		<view class="d-flex flex-column overflow-hidden">
			<view class="search-box">
				<view class="search-input">
					<image src="../../../../static/common/search-icon.png" class="search-icon"></image>
					<input type="text" v-model="keyword" 
							placeholder="输入要搜索的菜品"
							placeholder-class="placeholder" @input="handleKeywordInput">
				    <!-- <image v-if="keyword" src="../../../../static/images/common/image-delete.png" class="close-icon" @tap="clear" /> -->
				</view>
				<view class="ml-30" @click="custom()">搜索</view>
			</view>
			<scroll-view class="result" scroll-y>
				<template v-if="!result.length">
					<view class="section">
						<view class="header">
							<view class="title">历史搜索</view>
							<view class="subtitle">
								<!-- <image src="../../../../static/images/common/delete.png"></image> -->
								<view>清除</view>
							</view>
						</view>
						<view class="list">
							<view class="item" v-for="(item, index) in historySearch" :key="index" @tap="handleChoose(item)">
								{{ item.productName }}
							</view>
						</view>
					</view>
					<view class="section">
						<view class="header">
							<view class="title">热门推荐</view>
						</view>
						<view class="list">
							<view class="item" v-for="(item, index) in hotSearch" :key="index" @tap="handleChoose(item)">
								<view class="name">{{ item.productName }}</view>
								<image v-if="item.nameImage" :src="item.nameImage" class="name-image"></image>
							</view>
						</view>
					</view>
				</template>
				<template v-else>
					<view class="wrapper" style="padding: 0 30rpx 30rpx 0;">
						<view class="product" v-for="(item, index) in result" :key="index" @tap="handleChoose(item, true)">
							<view class="d-flex align-items-center">
								<image :src="imgurl + item.small_img_path" class="pro-image"/>
								<view class="pro-name">{{ item.spmc }}</view>
							</view>
							<view class="pro-price">￥{{ item.hyjg}}</view>
						</view>
					</view>
				</template>
			</scroll-view>
			</view>
	</uni-transition>
</template>

<script>
import uniTransition from '@/components/uni-transition/uni-transition.vue'

export default {
		name: 'Search',
		components: {
			uniTransition
		},
		props: {
			show: {
				type: Boolean,
				default: false
			},
			categories: {
				type: Array,
				default: () => []
			}
		},
		data() {
			return {
        imgurl:"http://cateapi.mzsale.cn/",
        tranStyles: {
					width: '100%',
					position: 'absolute',
					top: 0,
					left: 0,
					bottom: 0,
					backgroundColor: '#fff',
					zIndex: 998
				},
				hotSearch: [],
				historySearch: [],
				keyword: '',
				result: []
			}
		},

		methods: {
      //搜索
      custom(){
        this.$u.api.dishess({
          spmc:this.keyword,
          categoryid:'10',
          access_token:uni.getStorageSync('token'),
          vtype:'pos',
          fdbh:uni.getStorageSync('fdbh'),
          companyid:uni.getStorageSync('companyid'),
        }).then((res) =>{
          console.log('搜索结果',res)
          this.result=res.disheslist
        })
      },
      //取消
			hide() {
				this.keyword = ''
				this.result = []
				this.$emit('hide')
			},

			handleChoose(item, isSearch = false) {
				if(isSearch) {
					this.hide()
					this.$emit('choose', item)
					return
				}
				this.categories.forEach(category => {
					const find = category.products.find(product => product.id == item.productId)
					if(find) {
						this.hide()
						this.$emit('choose', find)
						return
					}
				})
			},
			handleKeywordInput(e) {
			},
			clear() {
				this.keyword=''
				this.result = []
			}
		}
	}
</script>

